import { AfterViewInit, Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { URLz } from 'src/app/enums/url.enum';
import { DonorGSBForm } from 'src/app/model/donor/donor-gsb.form';
import { FullTransactionForm, Transaction } from 'src/app/model/transaction/full.form';
import { environment } from 'src/environments/environment';
import { InfoComponent } from '../../donor/info/info.component';
import { GSBComponent } from '../../donor/gsb/gsb.component';
import { FullBasicDonorForm } from 'src/app/model/donor/full-basic-donor.form';
import { FullGSBDonorForm } from 'src/app/model/donor/full-donor-gsb.form';
import { TransDialogComponent } from './trans-dialog.component';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './trans-add.component.html',
  styleUrls: ['./trans-add.component.css'],
  providers: [
    FullTransactionForm,
    // For Child Components
    FullBasicDonorForm,
    FullGSBDonorForm,
  ]
})
// Typescript does not support Partial Class Concept and it will be able to do this
// in future
export class TransAddComponent extends TransDialogComponent implements OnInit, AfterViewInit {
  ngOnInit() {
    super.continueProcess = this.continueProcess
    super.ngOnInit();
    this.routerStrategy();
    this.systemSubscription();
    this.userForDonorSubscription();
  }
  ngAfterViewInit(): void {
    if (this._activeId == null) this.checkBatchDialog();
    else this.continueProcess();
  }
  _onSubmity() {
    super._onSubmity({
      body: (ps) => {
        if (ps._activeId) this._fs._form.addControl('id', new FormControl(this._activeId));
        ps.param.body = this._fs._form.getRawValue();
        const receipt_details = this.receiptDetails.value
        const newArray = [];
        receipt_details.forEach((receipt) => {
          if (!this.emptyCheck(receipt?.row_id)) {
            newArray.push(receipt);
          }
        });
        ps.param.body.receipt_details = newArray;
      },
       confirmation: this.confirmation,
      next: (ps, res) => {
        const id = res.data.id
        const heading = this._activeId ? 'Updated' : 'Created'
        const message = res?.message
        const receipt_no = res.data.receipt_no;
        this.confirmationDialog(id, heading, message, receipt_no)
      }
    })
  }
  // Here Function Hoisting is mandatory b/c Parent Class is calling this function
  continueProcess = () => {
    const h = this._ss.permission_data_local
    // TRANSACTION UPDATE CASE
    if (this._activeId != null && Number(this._activeId) > 0) {
      this.loadTemplate = false
      this.patchData();
    }
    // CASE HANDLE BY STATES GUARDS
    // LOCAL STORAGE SAVED || (SINGLE HIERARCHY  && OU PREFIX STATUS = OK)
    else if (h) {
      this.patchHierarchy()
      this.loadTemplate = this._ss.permission_data_local_status  = true
      // this.userForDonorSubscription();
    }
    // MUTIPLE HIERARCHY || DEALING BY DIALOG BOX
    else if (this._ss.hierarchy.status){
      this.hierarchyDialog()
    }
    // SERVER IS NOT AUTHORIZING FOR SUCH USER
    // SINGLE HIERARCHY BUT OU PREFIX NOT SET
    else if (!this._ss.hierarchy.status && !this._ss.hierarchy.ou_prefix_status){
      this._vs._toastr_error('Operating Unit', 'Please create prefix for selected OU')
      this.loadTemplate = false;
      localStorage.removeItem('permission_data_local');
      this._ss.permission_data_local = null;
      this._ss.permission_data_local_status = false;
    }

  }
  patchData() {
    this._http
      .get({
        url: environment.TRANSACTION,
        endpoint: URLz.TRANSACTION,
        resource: this._activeId,
        query: {
          receipt_type: this.receipt_type
        }
      }).subscribe((res: any) => {

        const data: Transaction = (this.data = res.data.row);

        // Transaction Top Properties
        this.receipt_number = data.receipt_number;
        this._fs._form.patchValue({
          id: data.id,
          currency_id: data.currency_id,
          event_id: data.event_id,
          is_advance: data.is_advance,
          receipt_type: data.receipt_type,
          hierarchy: data.hierarchy,
        });
        if(data?.hierarchy){
          this._http.org_id = data.hierarchy.organisation_id
          this._http.sys_id = data.hierarchy.system_id
        }
        this.is_advance = data.is_advance;
        if (!data?.hierarchy?.dco) {
          const group = this._fs._form.get('hierarchy') as FormGroup;
          group.removeControl('dco');
        }
        this._fs._form.addControl('id', new FormControl(data.id));
        // Transaction Donor
        this.patchDonor(data);
        this.fullTransactionForm.patchReceiptDetail(
          data.receipt_details,
          this.receipt_type, true
        );
        this.loadTemplate = true;
      });
  }
  patchDonor(data: Transaction) {
    const donor = this._fs._form.get('donor') as FormGroup;
    const additional_info = this._fs._form.get('additional_info') as FormGroup;
    const donorGSBForm = new DonorGSBForm(this.injector);
    const infoComponent = new InfoComponent(this.injector)
    const gSBComponent = new GSBComponent(this.injector)
    donor?.addControl('id', new FormControl(this._activeId));
    if (!data?.gsb && data?.donor) {
      this._fs._form.patchValue({
        additional_info: data.additional_info,
        is_advance: data.is_advance,
      });
      infoComponent.fillData(data);
    } else {
      if (!this._activeId) {
        const currentMobile = donor.get('mobile').value;
        donor.reset();
        additional_info.reset();
        donor.get('mobile').patchValue(currentMobile);
      }
    }
    if (data?.is_advance === 1 && data?.gsb) {
      this._fs._form.addControl(
        'gsb',
        this._fs._fb.group({ ...donorGSBForm.initForm().controls })
      );
      gSBComponent.fillData(data);
    }
  }
}
