import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialogConfig } from '@angular/material/dialog';
import { URLz } from 'src/app/enums/url.enum';
import { Transaction } from 'src/app/model/transaction/full.form';
import { RECEIPT_TYPE } from 'src/app/model/transaction/enum';
import { HierarchyForm } from 'src/app/model/transaction/hierarchy.form';
import { TransactionDetailComponent } from 'src/app/shared/components/dialogs/transaction-detail/transaction-detail.component';
import { TransactionDialogComponent } from 'src/app/shared/components/filters/transaction-dialog/transaction-dialog.component';
import { environment } from 'src/environments/environment';
import { HierarchyOnly } from 'src/app/interface/common/hierarchy';
import { BaseListClass } from 'src/app/class/base-list-class';

@Component({
  selector: 'di-trans-list',
  templateUrl: './trans-list.component.html',
  styleUrls: ['./trans-list.component.css']
})
export class TransListComponent extends BaseListClass implements OnInit, OnDestroy {
  receipt_type: RECEIPT_TYPE
  transURL = environment.TRANSACTION + URLz.TRANSACTION
  showTemplate = false
  // # FURTURE REFERENCE
  // static properties can also be used for STATE MGMT PURPOSE
  static lastSelectedHierarchy: HierarchyOnly
  subSearch: any = {};
  public globurl = window.location.href;
  ngOnInit() {
    this.setRecieptType();
    this._pathLocation = "/transaction/cash_cheque";
    this._component = "Transaction " + this.returnRecType(this.receipt_type)
    this.changeHierarchy();
    this.initTables();
  }
  initTables() {
    this._reset(URLz.$)
    this.initForm();
    this._tbls[URLz.$].columns = [
      'actions', // Print, Details, Edit, SMS, Email
      'name',
      'donor_mobile',
      'receipt_number',
      'receipt_type',
    ];
    this._tbls[URLz.$].columns.splice(3, 0, this.receipt_type == RECEIPT_TYPE.MATERIAL ? "date" : "amount")
    this._tbls[URLz.$].url = environment.TRANSACTION
    this._tbls[URLz.$].endpoint = URLz.TRANSACTION;
    this._formCreator(this._tbls[URLz.$]);
    this._refresh(URLz.$)
  }
  initForm() {
    const hierarchy = new HierarchyForm(this.injector);
    this._tbls[URLz.$].formFilter = this._fs._fb.group({
      from_date: [this._fhs._dateOnly(), this._vs._vals('From Date')],
      to_date: [this._fhs._dateOnly(), this._vs._vals('To Date')],
      ...hierarchy.initForm().controls,
      receipt_type: [this.receipt_type],
    })
    this._fs._form = this._fs._fb.group({
      hierarchy: this._tbls[URLz.$].formFilter
    })
  }
  // Resend Email
  _resendSms(id) {
    const smsObj = {
      id,
      receipt_type: this.receipt_type
    }
    this._http.create({
      body: smsObj,
      url: environment.TRANSACTION,
      endpoint: 'resend_sms'
    }).subscribe((res: any) => {
      this._swl.swal('SMS', res?.message)
    })

  }
  Switch(id, slip) {
    let url = '/blank/';
    if (this.globurl.indexOf('cash') != -1 || this.globurl.indexOf('deposit') != -1) {
      if (id) url += slip == 'colored' ? 'cash_cheque_color' : 'cash_cheque_black'
    }
    else if (this.globurl.indexOf('material') != -1) {
      if (id) url += slip == 'colored' ? 'material_color' : 'material_black'
    }
    url = this._router.serializeUrl(
      this._router.createUrlTree([url, { slipId: id }])
    );
    window.open(url, '_blank');
  }
  patchLastSelectedResult() {
    if (TransListComponent.lastSelectedHierarchy) {
      this.patchHiearchy(TransListComponent.lastSelectedHierarchy)
      this._reset(URLz.$)
      this._refresh(URLz.$)
    }
  }
  // HIEARCHY
  changeHierarchy() {
    this.openDialog('');
  }
  openDialog(title) {
    const config: MatDialogConfig = {
      panelClass: "dialog-responsive",
      data: { title, hideCheckBox: true }
    }
    const dialogRef = this._dialog.open(TransactionDialogComponent, config);
    dialogRef.afterOpened().subscribe(() => {
      if (TransListComponent.lastSelectedHierarchy) {
        this.patchHiearchy(TransListComponent.lastSelectedHierarchy)
      }
      else {
        const currentDate = this._fhs._dateOnly()
        this._tbls[URLz.$].formFilter?.patchValue({
          from_date: currentDate,
          to_date: currentDate
        })
      }
    })
    dialogRef.afterClosed().subscribe(() => {
      TransListComponent.lastSelectedHierarchy =
        this._tbls[URLz.$].formFilter?.value;
      this.showTemplate = true;
      this._reset(URLz.$);
      this._refresh(URLz.$)

    });
  }
  patchHiearchy(d: HierarchyOnly) {
    this._tbls[URLz.$].formFilter.patchValue({
      organisation_id: d.organisation_id,
      system_id: d.system_id,
      bg: d.bg,
      le: d.le,
      ou: d.ou,
      su: d.su
    })
    if (d.dco) {
      this._tbls[URLz.$].formFilter.patchValue({
        dco: d.dco
      })
    }
    if (d['from_date']) {
      this._tbls[URLz.$].formFilter.patchValue({
        from_date: d['from_date'],
        to_date: d['to_date']
      })
    }
  }
  detailDialog(id: string) {
    this._http
      .get({
        endpoint: URLz.TRANSACTION,
        url: environment.TRANSACTION,
        resource: id,
        query: { receipt_type: this.receipt_type }
      })
      .subscribe((res: any) => {
        const data: Transaction = res.data.row;
        const config: MatDialogConfig = {
          panelClass: "dialog-responsive",
          data
        }
        const dialogRef = this._dialog.open(TransactionDetailComponent, config);
        dialogRef.afterClosed().subscribe();
      });

  }
  setRecieptType() {
    const url = window.location.href;
    if (url.indexOf('cash') != -1) {
      this.receipt_type = RECEIPT_TYPE.CASH;
    } else if (url.indexOf('deposit') != -1) {
      this.receipt_type = RECEIPT_TYPE.DEPOSIT;
    } else if (url.indexOf('material') != -1) {
      this.receipt_type = RECEIPT_TYPE.MATERIAL;
    }
  }
  returnRecType(rt: number) {
    if (rt == 1) return '(Cash / Cheque)'
    else if (rt == 3) return 'Deposit'
    else if (rt == 4) return 'Material'
    else return rt
  }
  _switchs(id, rt = this.receipt_type) {
    let path = 'transaction/'
    if (id > 0 && rt) {
      if (rt == 1) path += 'cash_cheque_add'
      else if (rt == 3) path += 'deposit_add'
      else if (rt == 4) path += 'material_add'
      this._router.navigate([path, { id }])
    }
  }
  ngOnDestroy(): void {
    TransListComponent.lastSelectedHierarchy = undefined
  }
}
