import { Component, OnDestroy} from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialogConfig } from '@angular/material/dialog';
import { URLz } from 'src/app/enums/url.enum';
import { TransactionConfirmationComponent } from 'src/app/shared/components/dialogs/transaction-confirmation/transaction-confirmation.component';
import { Custom } from 'src/app/static/custom';
import { TransactionDialogComponent } from 'src/app/shared/components/filters/transaction-dialog/transaction-dialog.component';
import { TransSubscriptionComponent } from './trans-subscription.component';
import { OnInit } from '@angular/core';
import { PartialSubmit } from 'src/app/interface/common/partial-submit';
import { TransactionConfirmComponent } from 'src/app/shared/components/dialogs/transaction-confirm/transaction-confirm.component';

@Component({
  template: '',
})
export class TransDialogComponent extends TransSubscriptionComponent implements OnDestroy , OnInit{
  data: any;
  receipt_number = '';
  // continueProcess is assigned in Parent Class has Function Hoisting
  continueProcess; // Here Function Hoisting is mandatory
  resetForm() {
    if(this._fs._form.dirty){
      this._swl.formLeave.then((x) => {
          if (x.isConfirmed) {
            this.resetActionForm()
          }
        });
    } else {
      this.resetActionForm()
    }

  }
  resetActionForm(){
    const hierarchy = this._fs._form.get('hierarchy').value
    const receipt_date = this._fs._form.get('transaction_receipt_date').value
    this._ss.hierarchy = {...this._ss.hierarchy, ...hierarchy };
    this.rmvAllReceiptDetail();
    this._fs._form.reset();
    this.addReceiptDetail();
    this._fs._form.get('transaction_receipt_date').patchValue(receipt_date)
    this._fs._form.get('receipt_type').patchValue(this.receipt_type)
    this.patchHierarchy();
  }
  // SELECT HIEARCHY
  hierarchyDialog() {
    if(this._fs._form.dirty){
      this._swl.formLeave.then((x) => {
          if (x.isConfirmed) {
            this.hierarchyDialogAction()
          }
        });
    } else {
      this.hierarchyDialogAction()
    }
  }
  hierarchyDialogAction(){
    this.resetActionForm();
    const config: MatDialogConfig = {
      panelClass: "dialog-responsive",
      data:  {}
    }
    const dialogRef = this._dialog.open(TransactionDialogComponent, config);
    dialogRef.afterClosed().subscribe(() => {
      this.patchHierarchy()
      this.loadTemplate = true
    });
  }
  patchHierarchy(h = this._ss.permission_data_local) {
    if(!h && localStorage.getItem('permission_data_local')) {
      this._ss.permission_data_local = JSON.parse(localStorage.getItem('permission_data_local'))
      h = JSON.parse(localStorage.getItem('permission_data_local'))
    }

    const hierarchy = this._fs._form.get('hierarchy') as FormGroup;
    if(hierarchy.invalid){
      hierarchy.patchValue({
        organisation_id: h?.organisation_id,
        system_id: h?.system_id,
        bg: h?.bg,
        le: h?.le,
        ou: h?.ou,
        su: h?.su,
      });
    }
    if (this.emptyCheck(h?.dco)) hierarchy.patchValue({dco: h.dco})
    if(hierarchy?.get('dco')?.invalid && !h?.dco) hierarchy.removeControl('dco')
    if (+h?.system_id != 3) this._fs._form.removeControl('gsb')
    if(h?.currency_id) this._fs._form?.get('currency_id')?.patchValue(h?.currency_id)
    // For Array to use org system as well
    if(h?.organisation_id) this._http.org_id = h?.organisation_id
    if(h?.system_id) this._http.sys_id = h?.system_id
    if(!this._ss?.hierarchy?.status){
      this.loadTemplate = true
    }
  }
  confirmationDialog(id, heading, message, receipt_no) {
    const config: MatDialogConfig = {
      panelClass: "dialog-responsive",
      data: { id, heading, message ,receipt_no}
    }
    const dialogRef = this._dialog.open(TransactionConfirmationComponent, config);
    dialogRef.afterClosed().subscribe(() => {
      if (this._activeId != null) {
        const hierarchy = this._fs._form.get('hierarchy').value;
        this._ss.hierarchy = {
          ...this._ss.hierarchy,
          ...hierarchy
        }
        this._fs._form.reset();
        this.Switch()
      } else {
        this.resetActionForm();
        if(!this._ss.permission_data_local_status && this._ss?.hierarchy?.status){
          this.hierarchyDialog();
        }
      }
    });
  }
  checkBatchDialog() {
    this._http
      .get({ endpoint: URLz.CHECK_BATCH })
      .subscribe(
        (res: any) => {
          const data: { status: boolean; message: string } = res.data.row;
          if (!data.status) {
            const batchNumber =  res.data.row.batch_number ? res.data.row.batch_number : 0
            this._swl.transBatch(
              batchNumber,
              this._ss?.hierarchy?.profile?.name,
              () => {
                this.continueProcess();
              }
            )
          } else this.continueProcess();
        },
        // (error) => {
        //   window.open(
        //    'http://localhost:4200/blank/cash_cheque_color;slipId=562',
        //    '_blank'
        //   )
        // }
      )
  }
  // Not in Use
  confirmation = (ps: PartialSubmit) => {
    const data =this._fs._form.getRawValue()
    const config: MatDialogConfig = {
      panelClass: "dialog-responsive",
      data
    }
    const dialogRef = this._dialog.open(TransactionConfirmComponent, config);
    this.subscriptionArray.push(dialogRef.afterClosed().subscribe());
    dialogRef.afterClosed().subscribe({
      next: (result) => {
        if (result) {
          ps.confirmationAction(ps)
        }
        else {
          ps.confirmationDeny(ps)
        }
    }});
  }
  Switch() {
    if(this._activeId){
      let url;
      if (window.location.href.indexOf('material') != -1) url = 'material'
      else url = 'cash_cheque'
      this._router.navigate(['transaction/' + url])
      // window.open('http://localhost:4200/blank/cash_cheque_color;slipId=562', '_blank');
    }else {
      this.resetForm();
    }
  }
}
