import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ACTION } from 'src/app/enums/action.enum';

import { TransAddComponent } from './trans-add/trans-add.component';
import { TransListComponent } from './trans-list/trans-list.component';

const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: '',
        redirectTo: 'cash_cheque',
        pathMatch: 'full'
      },
      {
        path: "cash_cheque_add",
        component: TransAddComponent,
        data: {
          title: "Cash",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Transaction"},
            { title: "Cash"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "cash_cheque",
        component: TransListComponent,
        data: {
          title: "Cash / Cheque List",
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: "Transaction"},
            { title: "Cash / Cheque"},
            { title: "List" },
          ]
        }
      },
      {
        path: "deposit_add",
        component: TransAddComponent,

        data: {
          title: "Deposit",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Transaction"},
            { title: "Deposit"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "deposit",
        component: TransListComponent,
        data: {
          title: "Deposit List",
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: "Transaction"},
            { title: "Deposit"},
            { title: "List" },
          ]
        }
      },
      {
        path: "material_add",
        component: TransAddComponent,

        data: {
          title: "Material",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Transaction"},
            { title: "Material"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "material",
        component: TransListComponent,

        data: {
          title: "Material List",
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: "Transaction"},
            { title: "Material"},
            { title: "List" },
          ]
        }
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TransactionRoutingModule { }
