import { Component, Injector, OnInit } from '@angular/core';
import { URLz } from 'src/app/enums/url.enum';
import { BaseForm } from 'src/app/class/base.form';
import { FullTransactionForm } from 'src/app/model/transaction/full.form';
import { RECEIPT_TYPE } from 'src/app/model/transaction/enum';
import { environment } from 'src/environments/environment';
import { FullBasicDonorForm } from 'src/app/model/donor/full-basic-donor.form';
import { FullGSBDonorForm } from 'src/app/model/donor/full-donor-gsb.form';

@Component({
  template: '',
})
// For Injecting Service and Imports
export class TransDialogComponent extends BaseForm implements OnInit {
  // Injected Services
  fullTransactionForm: FullTransactionForm;
  fullBasicDonorForm: FullBasicDonorForm;
  fullGSBDonorForm: FullGSBDonorForm;

  // Transaction Properties
  RECEIPT_TYPEZ = RECEIPT_TYPE;
  receipt_type: RECEIPT_TYPE;
  is_advance = 1;
  _component: string;
  loadTemplate = false;
  constructor(public injector: Injector) {
    super(injector);
    this.fullTransactionForm = injector.get(FullTransactionForm);
    this.fullBasicDonorForm = injector.get(FullBasicDonorForm);
    this.fullGSBDonorForm = injector.get(FullGSBDonorForm);

  }
  ngOnInit() {
    this._activeId = this._fhs._getURLParam('id');
    this._fhs._pathLocation = '/transaction/list';
    this.param.endpoint = URLz.TRANSACTION;
    this.param.url = environment.TRANSACTION;
    this._vs.showWarning = true
    this.getTransationType();
    this._fs._form = this.fullTransactionForm.initForm(this.receipt_type);
    if (this.receipt_type == RECEIPT_TYPE.MATERIAL) {
      this._fs._form.removeControl('gsb');
      this._fs._form.removeControl('currency_id');
    }
    this.param.query = {
      receipt_type:this.receipt_type
    };
  }
  getTransationType() {
    const url = window.location.href;
    if (url.indexOf('cash') != -1) {
      this._component = 'Cash / Cheque Receipt';
      this.receipt_type = RECEIPT_TYPE.CASH;
    }
    if (url.indexOf('deposit') != -1) {
      this._component = 'Direct Deposit Receipt';
      this.receipt_type = RECEIPT_TYPE.DEPOSIT;
    } else if (url.indexOf('material') != -1) {
      this._component = 'Material Receipt';
      this.receipt_type = RECEIPT_TYPE.MATERIAL;
      this._fs._form.removeControl('gsb');
      this._fs._form.removeControl('currency_id');
    }
  }
  is(RT: RECEIPT_TYPE){
    return RT == this.receipt_type
  }
}
