import { Component } from '@angular/core';
import { FormArray, FormGroup } from '@angular/forms';
import { Transaction } from 'src/app/model/transaction/full.form';
import { RECEIPT_TYPE } from 'src/app/model/transaction/enum';
import { Custom } from 'src/app/static/custom';
import { ChangeEvent } from 'src/app/interface/common/change-event';
import { TransDialogComponent } from './trans-zbase.component';

@Component({
  template: '',
})
// HANDLING RECEIPT ARRAY
export class TransReceiptComponent extends TransDialogComponent {
  date: Date = new Date();
  // 1 Receipt Detail Initialization
  receiptDetail(tt = this.receipt_type): FormGroup {
    return this.fullTransactionForm.receiptDetail(tt);
  }
  // 2 Receipt Detail (Template Iteration)
  get receiptDetails(): FormArray {
    return this._fs._form?.get('receipt_details') as FormArray;
  }
  // 3 Receipt Detail Add
  addReceiptDetail(tt: any | RECEIPT_TYPE = this.receipt_type) {
    const arr = this._fs._form?.get('receipt_details') as FormArray;
    if (tt?.value?.donationMode == 2){
      arr.push(this.receiptDetail(RECEIPT_TYPE.CHEQUE))
    }
    else arr.push(this.receiptDetail())
  }
  // 4. Receipt Detail Copy
  copyData(d: any) {
    d.row_id = null;
    d.is_active = null;
    d.code = null;
    this.fullTransactionForm.patchReceiptDetail([d], this.receipt_type, false);
  }
  // 5. Receipt Detail Remove
  rmvReceiptDetail(index: number) {
    const arr = this._fs._form.get('receipt_details') as FormArray;
    arr.removeAt(index);
  }
  // 6. Receipt Detail Not in Use
  rmvAllReceiptDetail() {
    // Don't Use the Browser Submit Behaviour in Case of Array it looses the
    // Form Validation Sync incase of  FormArray
    const arr = this._fs._form.get('receipt_details') as FormArray;
    while (arr.length > 0) {
        arr.removeAt(0);
    }
    // arr.patchValue([{
    //   code: '',
    //   row_id: '',
    //   donationMode: this.receipt_type,
    // }])
  }
  // 7. Receipt Detail Cancel Entry from Array
  cancelReceiptDetail(index: number, item: FormGroup) {
    const cash = item.value;
    this._swl.transCancel(cash, this.receipt_type)
      .then(status =>  {
        if(status) {
          item.get('is_active').patchValue(2)
          // this.rmvReceiptDetail(index)
        }
      });
  }
  // 8. Receipt Detail Mode Change
  donationModeChanged(event: ChangeEvent, index: number,) {
    if (event?.event?.isUserInput) {
      const tranz: RECEIPT_TYPE = +event.id;
      const fr = this._fs._form.get('receipt_details') as FormArray;
      fr.insert(index, this.fullTransactionForm.receiptDetail(tranz));
      fr.removeAt(index + 1);
    }
  }
  // 9. Receipt Detail Add Disabled
  get addMoreDisabled() {
    let allDisabled = true;
    const trans: Transaction = this._fs._form.value;
    trans.receipt_details.forEach((receipt) => {
      if (
        (receipt.is_active == null && receipt.row_id == null) ||
        (receipt.is_active == 1 && receipt.row_id != null)
      ) {
        allDisabled = false;
      }
    });
    return allDisabled;
  }
  // 10. Receipt Detail Disable Submit when > than
  get exceedLimitDisable() {
    const trans: Transaction = this._fs._form.value;
    return trans?.receipt_details?.length >= 20;
  }
  // 11. Submit Disabled
  get submitDisabed(){
    const trans: Transaction = this._fs._form.value;
    let hasNotRowID = true
    trans?.receipt_details?.forEach(rec => {
      if(!this.emptyCheck(rec.row_id)){
        hasNotRowID = false
      }
    })
    return hasNotRowID;
  }
  // 12. Currency Prefix in Amount
  currencyPrefix = 'PKR ';
  currencyCount = 0;
  handleCurrencyChange(event: ChangeEvent){
    if(this.currencyCount == 0 || event.event.isUserInput){
      this.currencyCount++;
      this.currencyPrefix = event.obj.title + ' ';
    }
  }
  // 13. Bank Display Text
  bankCallBack(val){
    return val?.title + (val?.account_number ? ' - ' + val?.account_number : '')
  }
}
