
import { Component } from "@angular/core";
import { FormControl, FormGroup } from "@angular/forms";
import { NavigationEnd } from "@angular/router";
import { debounceTime, distinctUntilChanged } from "rxjs";
import { URLz } from "src/app/enums/url.enum";
import { DonorGSBForm } from "src/app/model/donor/donor-gsb.form";
import { RECEIPT_TYPE } from "src/app/model/transaction/enum";
import { Custom } from "src/app/static/custom";
import { TransReceiptComponent } from "./trans-xreceiptarray.component";

@Component({
  template: '',
})
export class TransSubscriptionComponent extends TransReceiptComponent {
  userForDonorSubscriptions;
  userForDonorSubscription() {
    if (!this._activeId && !this.userForDonorSubscriptions) {
      this.userForDonorSubscriptions = this._fs._form
      .get('additional_info.careOf').valueChanges.pipe(
          debounceTime(450),
          distinctUntilChanged()
        ).subscribe((mobile) => {
          if (this.emptyCheck(mobile)) {
            this._http.get({
              endpoint: URLz.CAREOF,
              query: {title: mobile}
            }).subscribe((res) => {
              const data = res.data.row;
              const gsb = this._fs._form.get('additional_info') as FormGroup;
              if (data?.name) {
                gsb.get('careOfName').patchValue(data?.name);
              } else {
                gsb.get('careOfName').patchValue('');
              }
            });
          }
        });
        this.subscriptionArray.push(this.userForDonorSubscriptions)
    }
  }
  systemSubscriptions
  systemSubscription() {
    const gsb = new DonorGSBForm(this.injector);
    this.systemSubscriptions = this._fs._form?.get('hierarchy').get('system_id')
      .valueChanges.subscribe((val) => {
      if (Custom.emptyCheck(val)) {
        if(!this.is(RECEIPT_TYPE.MATERIAL)) {
          // E-Raseed
          if (val != 3) {
            this._fs._form.removeControl('gsb');
            this._fs._form.get('is_advance').patchValue(0);
            this.is_advance = 0;
          } else {
            this._fs._form.get('is_advance').patchValue(1);
            this.is_advance = 1;
            this._fs._form.addControl(
              'gsb',
              this._fs._fb.group({
                ...gsb.initForm().controls,
              })
            );
          }
        } else if (this.is(RECEIPT_TYPE.MATERIAL)) {
          this._fs._form.removeControl('gsb');
          this._fs._form.removeControl('currency_id');
          this._fs._form.get('is_advance').patchValue(0);
          this.is_advance = 0;
        }
        const hierarchy = this._fs._form?.get('hierarchy') as FormGroup;
        // If System DCO
        if(val != '2') hierarchy.removeControl('dco');
        else{
          hierarchy.addControl(
            'dco',
            new FormControl('', this._vs._vals('Donation Cell Office'))
          )
        }
      }
    });
    this.subscriptionArray.push(this.systemSubscriptions)
  }
}
